body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tile-title {
    color: white;
    opacity: .7;
    font-weight: 700;
    position: absolute;
    bottom: 30px;
    left: 40px;
    -webkit-transition: bottom 0.3s, left 0.3s, opacity 0.3s;
    transition: bottom 0.3s, left 0.3s, opacity 0.3s;
    font-size: 1.2em;
    line-height: 1.2em;
}

.is-hovered .tile-title {
    bottom: 34px;
    left: 44px;
    opacity: .9;
}

.name {
    grid-area: name;
    background-color: #292F36;
}

.name .tile-title div:nth-child(2) {
    font-size: .6em;
    line-height: 1.2em;
}

.name.is-hovered .tile-title {
    left: 40px;
    bottom: 30px;
}

.about {
    grid-area: about;
    background-color: #058ED9;
}

.projects {
    grid-area: projects;
    background-color: #FF6B6B;
}

.tech {
    grid-area: tech;
    background-color: #FFE66D;
}

.contact {
    grid-area: contact;
    background-color: lightslategray;
}

.resume {
    grid-area: resume;
    background-color: #087F8C;
}

.page-wrapper {
    height: 100vh;
    width: 100vw;
    justify-content: center;
    max-width: 100%;
    text-align: center;
    max-height: 100%;
    background-color: #F7FFF7;
}

.grid-container {
    display: grid;
    grid-template-areas:
            'name name name projects projects projects'
            'about about about projects projects projects'
            'tech tech contact contact resume resume';
    grid-gap: 40px;
    height: 91%;
    width: 93%;
    margin: 0 auto;
    padding: 3% 0;
}
.grid-container > div {
    text-align: center;
    font-size: 30px;
    position: relative;
    box-shadow: 2px 1px 1px lightgrey;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.grid-container > div:before {
     content: '';
     position: absolute;
     border: white solid 12px;
     top: 5px;
     left: 5px;
     right: 5px;
     bottom: 5px;
     opacity: .4;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-property: border;
    transition-property: border;
 }

.grid-container > div:hover:before {
    border: white solid 16px;
    opacity: .9;
}

.grid-container > div:first-child {
    text-align: left;
    cursor: default;
    opacity: .9;
}

.grid-container > div:first-child:before  {
    border: white solid 16px;
    opacity: .9;
    -webkit-transition-property: none;
    transition-property: none;
    top: 7px;
    left: 7px;
    right: 7px;
    bottom: 7px;
}

.grid-container > div:first-child:hover:before  {
    border: white solid 16px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

